import React, {Component} from "react"
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
export default function Recommendations() {
    const settings = {
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "20px",
        slidesToShow: 3,
        speed: 500,
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: true,
                dots: true
              }
            },
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                initialSlide: 1
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
          ]
        
      };
    return(
        <section className="section" id="recommendations">
        <h1 className="recommendations">Recommendations</h1>
            <div style={{
                margin: "2em"
            }}>
            <Slider {...settings}>
                {reviews.map((d) =>(
                    <div className="card">
                    <div className="title is-6 card-body">{d.data}</div>
                    <div className="media-content" style={{textAlign: "center"}}>
                    <figure class="image is-64x64">
                        <img className="is-rounded" src={d.img}></img>
                    </figure>   
                        <p className="subtitle is-6">{d.name}</p>
                    </div>
                    </div>
                ))}
                </Slider>
            </div>
        </section>
    )
}

